import React from 'react';
import ImageUploading from 'react-images-uploading';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import DrawerAppBar from '../components/homeBar';
import { Typography } from '@material-ui/core';


function Verification() {
  const [images, setImages] = React.useState([]);
  const [idSubmitted, setIdSubmitted] = React.useState(false);

  const maxNumber = 69;

  const onChange = (imageList: any, addUpdateIndex: any) => {
    // data for submit
    console.log(imageList, addUpdateIndex);
    setImages(imageList);
  };

  const submitImages = () => {
    setIdSubmitted(state => true);
    console.log(images);
  }

  return (
    <Box component="div">
      <DrawerAppBar />
      {!idSubmitted ? (
        <>
      <Typography variant="h4" style={{
        marginTop: '204px', 
        marginBottom: '-172px',
        marginLeft: '672px'
      }}>Please Submit Your ID Document</Typography>
      <Box component="div" style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center', 
        marginTop: '256px'
      }}>
      
      <ImageUploading
        multiple
        value={images}
        onChange={onChange}
        maxNumber={maxNumber}
        dataURLKey="data_url"
      >
        {({
          imageList,
          onImageUpload,
          onImageRemoveAll,
          onImageUpdate,
          onImageRemove,
          isDragging,
          dragProps,
        }) => (
          // write your building UI
          <Box className="upload__image-wrapper">
            <Button
              variant="contained"
              style={isDragging ? { color: 'red' } : undefined}
              onClick={onImageUpload}
              {...dragProps}
            >
              Click or Drop here
            </Button>
            &nbsp;
            <Button variant="contained" onClick={onImageRemoveAll}>Remove all images</Button>
            {imageList.map((image, index) => (
              <Box key={index} className="image-item">
                <img src={image['data_url']} alt="" width="428" />
                <Box className="image-item__btn-wrapper" style={{
                    padding: '10px',
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'space-around'
                }}>
                  <Button variant="contained" onClick={() => onImageUpdate(index)}>Update</Button>
                  <Button variant="contained" className='ml-5' onClick={() => onImageRemove(index)}>Remove</Button>
                  <Button variant="contained" className='ml-5' onClick={() => submitImages()}>Submit</Button>
                </Box>
              </Box>
            ))}
          </Box>
        )}
      </ImageUploading>
      
      </Box>
      </>) : <Box>
                    <Typography variant='h1' style={{
                        marginTop: '420px',
                        marginLeft: '256px'
                    }}>ID Document Submitted for Review
                    <br />
                    <sub className='ml-lg-5'>In Demo, there's no review period, Back to Home</sub></Typography>
                </Box>
        }
    </Box>
  );
}

export default Verification;